<template>
    <section class="main-content">
        <div class="padding-t_10">
            <div class="left-menu-tab">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white textelipsis"><a data-toggle-tooltip="tooltip">{{ ResourceTypeName
                        }}</a></span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab fsmtabcontrol lefttabiconfix"
                    id="myTab" role="tablist">
                    <li class="nav-item ">
                        <a class="nav-link active left-menu" data-toggle="tab" href="#TicketDetails" id="tab-details"
                            role="tab" aria-controls="TicketText" aria-selected="true" title="Details"
                            @click="BindTabsData('tab-details')"><i class="fa fa-ticket" aria-hidden="true"></i>
                            {{ $t('Details') }}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" data-toggle="tab" id="tab-resources" href="#Activities" role="tab"
                            aria-controls="Activities" aria-selected="false" title="Resource"
                            @click="BindTabsData('tab-resources')"><i class="fa fa-user" aria-hidden="true"></i>
                            {{ $t('Resource') }}</a>
                    </li>

                    <li class="nav-item">
                        <a class="nav-link left-menu" data-toggle="tab" id="tab-serviceAppointment" href="" role="tab"
                            aria-controls="ServiceAppointment" aria-selected="false" title="ServiceAppointment"
                            @click="BindTabsData('tab-serviceAppointment')"><i class="fa fa-calendar"
                                aria-hidden="true"></i> {{ $t('ServiceAppointment') }}</a>
                    </li>
                </ul>
            </div>


            <div class="right-content">
                <div v-if="!isResourceTabClicked" class="col-md-12 p-0">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-actions float-right">
                            <!-- <a href="javascript:;" title="Add" id="AddResPopUp" v-if="isAddResourcePopUp" 
                            class="p-action-btn text-white" v-on:click="openPopupForSetupTickectSource()"><i
                                    class="fa fa-plus"></i><br />{{ $t('Add')
                                    }}</a>                        

                            <a href="javascript:;" v-on:click="DeleteSetupTickectSourceList()" id="DeleteMultiple"
                                class="p-action-btn text-white" v-if="isDeleteButtonVisible"><i
                                    class="fa fa-trash"></i><br />{{ $t('Delete') }}</a> -->

                            <RouterLink :to="'/Ticketing/ResourceType'" title="Back"
                                class="p-action-btn text-white d-md-inline-block mble_50" data-toggle-tooltip="tooltip"
                                data-original-title="Back to list"><i class="fa fa-angle-double-left"
                                    aria-hidden="true"></i><br>{{ $t('Back to list ') }}</RouterLink>
                        </span>
                    </div>
                </div>

                <div id="ResourceTypeData" class="col-md-12 p-0 mt-2">

                    <div class="card mb-3 probx">
                        <div class="card-header">
                            <h2>Details</h2>
                        </div>
                        <div class="card-body">
                            <div class="row" v-for="(item, index) in ResourceTypeList" v-bind:key="index">
                                <div class="col-md-6 col-lg-3">
                                    <div class="form-group"><label><b>{{ $t('ResourceTypeName') }}:</b>
                                            {{ item.RESOURCE_TYPE_NAME }}</label></div>
                                </div>
                                <div class="col-md-6 col-lg-3">
                                    <div class="form-group">
                                        <label>
                                            <b>{{ $t('Status') }}:</b> <span v-if="item.STATUS_ID == 1002">{{
                                                $t('"Inactive"') }}</span>
                                            <span v-if="item.STATUS_ID == 1001">{{ $t('Active') }}</span>
                                        </label>
                                    </div>
                                </div>

                                <div class="col-md-12 col-lg-12">
                                    <div class="form-group"><label><b>{{ $t('Description') }}:</b>
                                            {{ item.DESCRIPTION }}</label></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="text-danger text-center no-record float-left w-100" v-if="noRecord">
                    {{ $t('NoRecordfound') }}
                </div>

            </div>

            <div id="ResourceData" class="right-content">
                <tg-list v-show="IsGroupView" :IsShowAction="false" ModuleName="Ticketing"
                    SubModuleCode="FSM_Resource_Type_Detail" IdentityColumn="RESOURCE_ID" :LegendArray="[]"
                    :HeaderButtons="listheaderbuttons" :RenderRowActionMethod="() => { }" :widgets="[]" :listType="['List']"
                    :ListData="ResourceList" :HeaderData="Headers" :TotalRecords="TotalRecords"
                    :ListDataCallBackFunction="FetchResources" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')">
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'status_id'">
                            <a class="inactive-disabled">
                                <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                                    v-if="data.row.STATUS_NAME == 'Active'">
                                    {{ data.row.STATUS_NAME }}
                                </span>
                                <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                                    v-if="data.row.STATUS_NAME == 'Inactive'">
                                    {{ data.row.STATUS_NAME }}
                                </span>
                            </a>
                        </template>
                    </template>
                </tg-list>
            </div>

            <div id="serviceAppointmentData" class="right-content">
                <tg-list v-show="IsGroupView1" :IsShowAction="true" ModuleName="Ticketing"
                    SubModuleCode="FSM_Resource_Type_Detail" IdentityColumn="id" :LegendArray="[]"
                    :RenderRowActionMethod="() => { }" :widgets="[]" :listType="['List']" :ListData="ServiceAppointmentList"
                    :HeaderButtons="Servicelistheaderbuttons" :HeaderData="SAHeaders" :TotalRecords="TotalRecords"
                    :ListDataCallBackFunction="FetchData" :SortExp="SortExp" :SortBy="SortBy"
                    :NorecordfoundText="$t('NoRecordfound')">
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'status_id'">
                            <listing-status :isfor="'MapStatus'" v-if="isCRMShowStausDrop(data.row.statusId, 'Ticketing')"
                                ref="updatestatus" v-bind:dataid="data.row.id" data-controller="Crm"
                                v-bind:statuslist="StatusListForListing" v-bind:item="data.statusId" data-action="index"
                                v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'InActive'"
                                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                            </listing-status>
                            <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                                <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                                    {{ data.row[data.column.COLUMN_NAME] }}
                                </span>
                            </a>
                        </template>
                    </template>
                </tg-list>
            </div>


        </div>
        <AddResourcePopup v-if="AddResourcePopup" :ResourceTypeId="ticketSourceId" @ticketId="getTicketId">
        </AddResourcePopup>
    </section>
</template>

<script>

import DataServices from "@/services/DataService";
import Schema from "@/views/ResourceType/ResourceTypeSchema";
import AddResourcePopup from '../../components/Modal/AddResourcePopup.vue'
export default {
    components: {
        AddResourcePopup
    },
    data() {
        return {
            user_id: null,
            ids: 0,
            isResourceTabClicked: false,
            AddResourcePopup: false,
            companyId: null,
            ResourceHeadingName: null,
            isDeleteButtonVisible: false,
            isAddResourcePopUp: false,
            isServiceAppointmentPopUp: false,
            IsGroupView: true,
            IsGroupView1: true,
            noRecord: false,
            TotalRecords: 0,
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            SortBy: '',
            SortExp: '',
            TicketId: null,
            ResourceTypeList: [],
            ServiceAppointmentList: [],
            listheaderbuttons: Schema.ResourceDetailPagebuttons,
            Servicelistheaderbuttons: [],
            ResourceList: [],
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            Headers: [{
                "COLUMN_NAME": "USERNAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "User",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "alias_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Alias Name",
                "DISPLAY_ORDER": 2,
            },
            {

                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }


            }
            ],

            SAHeaders: [{
                "COLUMN_NAME": "SA_Number",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "SA Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "Subject",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Subject",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "resources",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resource(S)",
                "DISPLAY_ORDER": 2,
            },
            {

                "COLUMN_NAME": "SA_Scheduled_StartTime",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Scheduled Start Time",
                "DISPLAY_ORDER": 3,
            },
            {

                "COLUMN_NAME": "SA_Scheduled_EndTime",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Scheduled End Time",
                "DISPLAY_ORDER": 4,
            },
            {

                "COLUMN_NAME": "Status",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "status",
                "DISPLAY_ORDER": 5,
            }
            ],

        }
    },
    created: async function () {
        debugger;
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        vm.ids = vm.TicketId;
        vm.TicketId = vm.$route.params.ResourceTypeId;
        vm.ResourceTypeName = vm.$route.params.ResourceTypeName;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.FetchData();
    },
    methods: {
        getTicketId(value) {
            debugger;
            var vm = this;
            vm.ids = value;
        },
        getStatusClassList: function (status) {
            debugger;
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        BindTabsData: async function (tabIdentifier) {
            var vm = this;
            debugger;
            //var clickedTabId = event.target.id;           
            if (tabIdentifier == "tab-details") {
                vm.isResourceTabClicked = false;
                vm.FetchData();
            }
            else if (tabIdentifier == "tab-resources") {
                vm.FetchResources();
                vm.isResourceTabClicked = true;
                vm.isDeleteButtonVisible = true;
                vm.isAddResourcePopUp = true;
                vm.isServiceAppointmentPopUp = false;
            }
            else if (tabIdentifier == "tab-serviceAppointment") {
                vm.FetchServiceAppointment();
                vm.isDeleteButtonVisible = false;
                vm.isAddResourcePopUp = false;
                vm.isServiceAppointmentPopUp = false;
            }
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchData: async function () {
            var vm = this;
            var url = `ResourceTypeid=${vm.TicketId}`;
            await DataServices.FetchResourceTypeById(url).then(function (response) {

                if (response.data != null) {
                    vm.ResourceTypeList = response.data.result.data;

                    if (vm.ResourceTypeList.length > 0) {
                        vm.NoRecord = false;
                    }
                    else {
                        vm.NoRecord = true;
                    }
                }
                else {
                    vm.NoRecord = true;
                }                
                vm.noRecord = false;
                $("#ResourceTypeData").show();
                $("#ResourceData, #serviceAppointmentData,#AddResPopUp,#DeleteMultiple,#AddServiceAppointment").hide();
            })
        },
        commonHeaderButtonClick: function (event) {
            debugger;
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteSetupTickectSourceList();
                    break;
                case "openPopupForSetupTickectSource":
                    this.openPopupForSetupTickectSource(null);
                    break;
                case "openResourceTypeList":
                    this.openResourceTypeList(null);
                    break;
            }
        },
        openPopupForSetupTickectSource: function (id) {
            var vm = this;
            if (id == undefined)
                id = 0;
            this.TicketId = id;
            this.AddResourcePopup = !this.AddResourcePopup;
        },
        openPopupForServiceAppointment: function () {
            debugger;
            var vm = this;
            vm.$router.push({ name: 'ManageServiceAppointment' });
        },
        openResourceTypeList: function () {
            debugger;
            var vm = this;
            vm.$router.push({ name: 'ResourceTypeList' });
        },

        FetchResources: async function () {
            debugger;
            var vueObj = this;
            if (vueObj.TicketId == 0) {
                vueObj.TicketId = vueObj.ids;
            }
            var url = `ResourceTypeid=${vueObj.TicketId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}`;
            await DataServices.FetchResourcesByResourceTypeById(url).then(function (response) {
                if (response.data.status != null) {
                    debugger;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.ResourceList = response.data.result.data;
                    vueObj.listheaderbuttons[1].isdisabled=true;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;

                    if (vueObj.ResourceList.length > 0) {
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.NoRecord = false;
                    }

                    else {
                        vueObj.NoRecord = true;
                    }
                }
                else {
                    vueObj.NoRecord = true;
                }               
                vueObj.noRecord = false;
                $("#ResourceTypeData, #AddServiceAppointment, #serviceAppointmentData").hide();
                $("#ResourceData,#AddResPopUp,#DeleteMultiple").show();
            })
        },

        FetchServiceAppointment: async function () {
            debugger;
            var vueObj = this;
            var url = `resourcetypeId=${vueObj.TicketId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}`;
            await DataServices.FetchResourceTypeId(url).then(function (response) {
                if (response.data.status != null) {
                    vueObj.ServiceAppointmentList = response.data.result.data;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;

                    if (vueObj.ServiceAppointmentList.length > 0) {
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.NoRecord = false;
                    }

                    else {
                        vueObj.NoRecord = true;
                    }
                }
                else {
                    vueObj.NoRecord = true;
                }
                vueObj.noRecord = false;
                $("#ResourceTypeData").hide();
                $("#ResourceData").hide();
                $("#serviceAppointmentData, #AddServiceAppointment").show();
            })
        },

        DeleteSetupTickectSourceList: function (event) {
            debugger;
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Resource")), function () {
                    debugger;
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_resource"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchResources();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Resource')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Resource')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },

        UpdateStatus: async function (status) {
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("Confirm"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var url = `id=${val}&status_id=${status}&isFor=${'group'}`;
                    DataServices.updatestatus(url).then(response => {
                        if (response.data == true) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchData();

                        }

                    })


                })
            }
        }
    }
}
</script>
